.scroll_watch{
  height: 3px;
  position: fixed;
  top: 0;
  z-index: 1000;
  background-color: var(--color-primary);
  width: 100%;
  scale: 0 1;
  transform-origin: left;

  animation: scroll_watch linear;
  animation-timeline: scroll();
}
@keyframes scroll_watch {
  to{ scale: 1 1;}
}



header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}
.header_container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* CTA */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* HeaderSocials css */
.header_socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}


.header_socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/* ME */
.me-pic {
  /* background: linear-gradient(var(--color-primary), transparent); */
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 12rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 3rem 3rem;
  overflow: hidden;
 

  padding: 0rem 0rem 0rem 1.5rem;
}
.me-pic:hover{
  transform: scale(1.1);
 
}
/* Scroll Down */
.scroll_down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}
/* .scroll_down::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
} */
.text-light {
  font-size: 1.5rem;
}

/* Media Queries(Medium) */
@media screen and (max-width: 1024px) {
  header {
    height: 100vh;
  }
}

/* Media Queries(Small) */
@media screen and (max-width: 600px) {
  .header_socials,
  .scroll_down {
    display: none;
  }
}
